import { projectsData } from './projectsData.js';

class ProjectPage {
    constructor() {
        this.projectId = this.getProjectIdFromUrl();
        this.projectData = projectsData[this.projectId];
        this.initializePage();
    }

    getProjectIdFromUrl() {
        const urlParams = new URLSearchParams(window.location.search);
        return urlParams.get('id');
    }

    initializePage() {
        if (!this.projectData) {
            window.location.href = '/projects.html';
            return;
        }

        document.title = `${this.projectData.title} - Lahcene Studio`;
        this.setupHeroVideo();
        this.setupProjectInfo();
        this.setupContent();
        this.setupNextProject();
        this.initializeAnimations();
    }

    setupHeroVideo() {
        const heroVideo = document.getElementById('hero-video');
        heroVideo.src = this.projectData.mainVideo;
    }

    setupProjectInfo() {
        // Set title
        document.querySelector('.project-title').textContent = this.projectData.title;
        
        // Set details
        document.querySelector('.year').textContent = this.projectData.details.year;
        
        // Set tools
        const toolsContainer = document.querySelector('.tools');
        this.projectData.details.tools.forEach(tool => {
            const toolTag = document.createElement('span');
            toolTag.className = 'tool-tag';
            toolTag.textContent = tool;
            toolsContainer.appendChild(toolTag);
        });

        // Set role
        document.querySelector('.role').textContent = this.projectData.details.role;

        // Set description
        document.querySelector('.project-description').textContent = this.projectData.description;
    }

    setupContent() {
        const contentContainer = document.querySelector('.project-content-blocks');
        
        this.projectData.content.forEach(block => {
            const blockElement = document.createElement('div');
            blockElement.className = 'content-block';

            switch (block.type) {
                case 'text':
                    blockElement.innerHTML = `<p>${block.content}</p>`;
                    break;
                case 'video':
                    blockElement.innerHTML = `
                        <video src="${block.url}" autoplay loop muted playsinline></video>
                        ${block.caption ? `<div class="caption">${block.caption}</div>` : ''}
                    `;
                    break;
                // Add more content types as needed
            }

            contentContainer.appendChild(blockElement);
        });
    }

    setupNextProject() {
        // Get all project IDs
        const projectIds = Object.keys(projectsData);
        const currentIndex = projectIds.indexOf(this.projectId);
        const nextIndex = (currentIndex + 1) % projectIds.length;
        const nextProjectId = projectIds[nextIndex];
        const nextProject = projectsData[nextProjectId];

        const nextProjectLink = document.querySelector('.next-project-link');
        nextProjectLink.textContent = nextProject.title;
        nextProjectLink.href = `project-template.html?id=${nextProjectId}`;
    }

    initializeAnimations() {
        // Add initial animations
        document.querySelectorAll('.project-title, .project-details, .project-description')
            .forEach(el => el.classList.add('fade-in'));

        // Setup scroll animations for content blocks
        const observer = new IntersectionObserver(
            (entries) => {
                entries.forEach(entry => {
                    if (entry.isIntersecting) {
                        entry.target.classList.add('visible');
                    }
                });
            },
            { threshold: 0.1 }
        );

        document.querySelectorAll('.content-block').forEach(block => {
            observer.observe(block);
        });
    }
}

// Initialize the page
document.addEventListener('DOMContentLoaded', () => {
    new ProjectPage();
});
